/* purgecss start ignore */

.chats_page {
  display: flex;
  justify-content: center;
  margin-top: 70px;
  width: 100%;
  height: calc(100vh - 70px);
}

.chats_page .partition {
  /* min-height: 400px; */
  overflow-y: auto;
  display: flex;
}

.user_name {
  padding: 10px 0;
  background-color: #4c4c6dc7;
  color: #ffffff;
  display: grid;
  justify-content: center;
}

.chat_list {
  flex: 1 1;
  background-color: #4c4c6d9a;
  display: flex;
  flex-direction: column;
  /* background-image: linear-gradient(#a32100, #00a2a8); */
}

.chat_wrapper {
  height: 100%;
}

.chat {
  background-color: #ffffffcb;
  margin-top: 0.1rem;
  padding: 0.4rem;
  display: flex;
  justify-content: center;
  font-size: 0.8rem;
  cursor: pointer;
}

.chat_window {
  flex: 3 1;
  height: 100%;
  width: 100vw;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  /* background-color: #2c0025; */
  background-image: linear-gradient(#402662, #3900a6);
  /* background-image: linear-gradient(#699ff6, #000066); */
}

.chat_window .top {
  background-color: #4C4C6D;
  color: #ffffff;
  padding: 10px;
  display: flex;
  justify-content: center;
}

.chats_display {
  flex: 1 1;
  /* max-width: 900px; */
  /* margin: auto; */
  /* padding: 30px; */
  /* height: 100%; */
  /* border-radius: 10px; */
  /* overflow-y: scroll; */
  /* margin-bottom: 24px; */
}

.scroll-y {
  overflow-y: auto;
}

.message_box {
  /* background-color: #4e4949; */
  min-width: 38.4px;
  max-width: 70%;
  margin: 4px 10px;
}

.message_label {
  color: #ffffff57;
  font-size: 0.8rem;
}

.message {
  /* background-color: #ac5757; */
  margin: 0;
  padding: 10px 16px;
  border-radius: 19.2px;
}

.left {
  float: left;
  clear: both;
}

.message_left {
  background-color: #ffffff23;
  color: #ffffffe1;
  margin-top: 2px;
}

.right {
  float: right;
  clear: both;
}

.message_right {
  background-color: #ffffffb6;
  color: #4C4C6D;
}

.middle {
  /* background-color: #73f3e2; */
  color: #ffffff;
  display: grid;
  float: unset;
  clear: both;
  border-radius: 5px;
  padding: 4px;
  max-width: 100%;
  justify-content: center;
}

.send {
  display: flex;
  height: 3rem;
  /* margin: 4px; */
}

#chats-messageInp {
  -webkit-box-decoration-break: unset;
  box-decoration-break: unset;
  flex: 1 1;
  min-height: 100%;
  border: 0;
  padding: 0 1rem;
}

#chats-btn {
  border-radius: 0;
  border: 0;
}

.noCurrentChat {
  color: #ffffff;
  margin-top: 3rem;
  font-size: 4rem;
  opacity: 0.4;
  align-self: center;
  cursor: default;
}

.addFriend {
  display: flex;
  flex-direction: column;
  margin: 0.2rem 0.1rem;
}

.addFriend input {
  padding: 0.4rem;
  margin: 0.2rem 0;
  -webkit-box-decoration-break: unset;
  box-decoration-break: unset;
  border: 0;
}

@media (max-width: 600px){
  header {
    display: none;
  }

  .chats_page{
    height: 100vh;
    margin: 0;
  }

  .chat_window{
    min-width: 100vw;
  }
}

